import { Component, OnDestroy, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { BehaviorSubject, Subscription, firstValueFrom } from 'rxjs';
import { ChqWidgetsButtonModel } from 'src/app/model/chq-widgets-button-model';
import { CommonService, i18nInfo } from 'src/app/services/common/common.service';
import { MonitorService } from 'src/app/services/monitor-service/monitor.service';
import { RepairEstimateService } from 'src/app/services/repair-estimate/repair-estimate.service';
import { BookingService } from 'src/app/services/booking/booking.service';
import { RepairEstimateOperationComponent } from 'src/app/dialogs/repair-estimate-operation/repair-estimate-operation.component'
import { RepairEstimateOperationService } from 'src/app/services/repair-estimate-operation/repair-estimate-operation.service';
import { SeverityLevel } from '@microsoft/applicationinsights-web';
import { GtMotiveEstimationDialogComponent } from 'src/app/dialogs/gt-motive-estimation-dialog/gt-motive-estimation-dialog.component';
import { Modules } from 'src/app/model/chq-upload-model';
import { RequestApprovalDialogComponent } from 'src/app/dialogs/request-approval-dialog/request-approval-dialog.component';
import { MessagesService } from 'src/app/services/message/messages.service';
import { ChqWidgetsDropdownModel } from 'src/app/model/chq-widgets-dropdown-model';
import { TranslateService } from '@ngx-translate/core';
import { CalendarService } from 'src/app/services/calendar/calendar.service';
import { EstimateApprovalComponent } from 'src/app/dialogs/estimate-approval/estimate-approval.component';
import { NotifyService } from 'src/app/services/notify/notify.service';

import * as dayjs from 'dayjs';


@Component({
  selector: 'estimate',
  templateUrl: './estimate.component.html',
  styleUrls: [ './estimate.component.scss' ]
})

export class EstimateComponent implements OnInit, OnDestroy {
  private subscription: Subscription;
  public editEstimateModel: ChqWidgetsButtonModel = {
    label: 'edit_estimate',
    type: 'outline',
    icon: 'edit'
  };

  public requestStatusModel: ChqWidgetsButtonModel = {
    label: 'self_approval',
    type: 'outline',
    icon: 'clock-new'
  };

  public addOperationModel: ChqWidgetsButtonModel = {
    label: 'add_operation',
    type: 'outline',
    icon: 'plus'
  }

  public bookInModel: ChqWidgetsButtonModel = {
    label: 'book_in',
    type: 'primary',
    icon: ''
  }

  public saveEditedModel: ChqWidgetsButtonModel = {
    label: 'done',
    type: 'primary',
    icon: ''
  }

  public cancelButtonModel: ChqWidgetsButtonModel = {
    label: 'Cancel Booking',
    type: 'outline-danger',
    onclick: this.cancelBooking.bind(this)
  }

  public showData: boolean = false;
  public editEstimate: boolean = false;
  private domainId: number;
  public estimateResponse: any;
  public caseGuid: string = '';
  public repairStatus: string = '';
  public communications: number[] = [];
  public inspectionId: number;
  public vehicleId: number;
  public showInSmallDevice: boolean;
  private email: string = '';
  public repairGuid: string = '';
  public repairId: string = '';
  public estimateSource: string = '';
  public vehicleObj: any;
  private repairNumber: string = '';
  private claimNumber: string = '';
  private organizationEmail: string = '';
  private organizationPhone: string = '';
  private organizationCountryCode: string = '';
  private organizationName: string = '';
  public dropdownModelFilter: ChqWidgetsDropdownModel = {
    options: [
    ],
    placeHolder: this.translationService.instant('options'),
    label: '',
    appearance: 'button',
    placeHolderIcon: 'settings',
    name: 'option',
    displayValue: 'label',
    selectedOption: {}
  };
  i18n: i18nInfo;
  communicationChannels = [
    {},
    { dropdownItemIcon: 'whatsapp', color: 'var(xa-white) !important;', label: this.translationService.instant('whatsapp') },
    { dropdownItemIcon: 'email', color: 'var(xa-white) !important;', label: this.translationService.instant('email') },
    { dropdownItemIcon: 'sms', color: 'var(xa-white) !important;', label: this.translationService.instant('sms') }
  ]
  public dropdownApprovalModelFilter: ChqWidgetsDropdownModel = {
    options: [
      { dropdownItemIcon: 'whatsapp', color: 'var(xa-white) !important;', label: this.translationService.instant('whatsapp') },
      { dropdownItemIcon: 'email', color: 'var(xa-white) !important;', label: this.translationService.instant('email') },
      { dropdownItemIcon: 'sms', color: 'var(xa-white) !important;', label: this.translationService.instant('sms') }
    ],
    placeHolder: 'request_approval',
    label: '',
    hideDropdownIcon: true,
    appearance: 'button',
    placeHolderIcon: 'send',
    name: 'option',
    displayValue: 'label',
    selectedOption: {}
  };

  userPermission: any = {};
  repairTypesArray: Array<string> = [];
  afterOperation: boolean = false;
  isRepairCreatedFromCase: boolean = false;

  bookingId: string = '';
  invoiceStatus: string = '';
  /**
   * show delete
   */
  get showDelete(): boolean {
    if (this.repairId && this.repairStatus?.toLowerCase() === 'draft') {
      return true;
    } else if (this.repairId && this.caseGuid && this.isRepairCreatedFromCase == true && this.repairStatus?.toLowerCase() == 'repairestimation') {
      return true;
    }
    return false;
  }

  /**
    Constructor for the component.
    @param {CommonService} commonService - The CommonService instance.
    @param {RepairEstimateService} repairEstimateService - The RepairEstimateService instance.
    @param {ActivatedRoute} route - The ActivatedRoute instance.
    @param {MatDialog} dialog - The MatDialog instance.
    @param {BookingService} bookingService - The BookingService instance.
    @param {RepairEstimateOperationService} repairEstimateOperationService - The RepairEstimateOperationService instance.
    @param {MonitorService} monitorService - The MonitorService instance.
    @param {Router} router - The Router instance.
  */
  constructor(
    public commonService: CommonService,
    private repairEstimateService: RepairEstimateService,
    private route: ActivatedRoute,
    public dialog: MatDialog,
    private bookingService: BookingService,
    private translationService: TranslateService,
    private repairEstimateOperationService: RepairEstimateOperationService,
    private monitorService: MonitorService,
    private messageService: MessagesService,
    private calendarService: CalendarService,
    private router: Router,
    private notifyService: NotifyService) {
    if (this.commonService.accessRight.closed) {
      this.commonService.accessRight = new BehaviorSubject<boolean>(true);
    }

    if (this.commonService.activeItem.closed) {
      this.commonService.activeItem = new BehaviorSubject<string>('Booking');
    } else {
      this.commonService.activeItem.next('Booking');
    }

    this.commonService.userProfileData.subscribe((res) => {
      if (res && res.data) {
        const { userPermission, email } = res.data;
        this.email = email;
        this.userPermission = userPermission || {};
      }
    });
    this.i18n = this.commonService.geti18nInfo();
    this.route.parent?.params?.subscribe((data) => {
      if (this.repairGuid != '' && this.repairGuid !== data['id']) {
        // Run all functions that are in ngOnInit so that it reflects all details of new repair
        this.monitorService.logEvent('ngOnInit', [ 'EstimateComponent', 'addenda-repair' ]);
        this.commonService.updatePage('/repair/customers/0', 'add-customer');
        this.repairGuid = this.route.parent?.snapshot.paramMap.get('id');
        this.getBookingRepairDetails();
      }
      setTimeout(()=>{
        if(this.router.url.indexOf('parts')!==-1){
          const element = document.querySelector('chq-tabs .active') as HTMLElement;
          element.click();
        }
      }, 300);
    });
  }

  /**
   * handleSelectedOption
   */
  handleSelectedOption($event: any): void {
    this.showRequestApprovalDialog($event?.value?.label);
  }

  /**
   * go to gtEstimate
   */
  redirectToGtEstimate(): void {
    this.commonService.showLoading();
    this.monitorService.logEvent('redirectToGtEstimate', [ 'ChqRepairEstimateComponent', 'addenda-quote', {
      caseId: this.caseGuid,
      status
    } ]);
    if (this.route.snapshot.queryParams['send'] && this.route.snapshot.queryParams['send'] == 'true') {
      this.router.navigate(
        [],
        {
          relativeTo: this.route,
          queryParams: { send: 'false' },
          queryParamsHandling: 'merge'
        });
    }
    this.repairEstimateService.createEstimateFromRepair(this.repairGuid).subscribe({
      next: (resp: any) => {
        if (resp) {
          location.href = resp.data.url;
          sessionStorage.setItem('update', '1');
          sessionStorage.setItem('current-url', this.router.url.toString());
          sessionStorage.setItem('initial-repair-types', JSON.stringify(this.repairTypesArray));
        }
      },
      complete: () => {
        this.commonService.hideLoading();
      }
    });
  }

  /**
   * show email dialog
   *
   */
  async showRequestApprovalDialog(channel: string): Promise<any> {
    this.commonService.showLoading();
    const res = await firstValueFrom(this.notifyService.getNotifyTemplate(this.repairGuid, this.domainId, 4));
    this.commonService.hideLoading();
    const subjectText = `<strong class='text-black-medium'>Request approval: </strong> <strong class='text-black-medium'>${this.repairNumber} / </strong> <strong class='text-black-medium'>${this.vehicleObj.vehicleMake} / </strong>
    <strong class='text-black-medium'>${this.vehicleObj.vehicleModel} / </strong> <strong class='text-black-medium'>${this.vehicleObj.licensePlateNumber}</strong> ${this.claimNumber ? `<strong class='text-black-medium'> / ${this.claimNumber} </strong>` : ''}`;
    let title = 'send_message';
    let emailSubject = '';
    let body = '';
    if(channel?.toLowerCase() === 'email') {
      title = 'send_email';
      emailSubject = subjectText;
      body = res[1].data.body
        .replace('{{firstname}} {{lastname}}', this.organizationName)
        .replace('{{make}}', res[0].data.vehicle?.vehicleMake)
        .replace('{{model}}', res[0].data.vehicle?.vehicleModel)
        .replace('{{licensenumber}}', res[0].data.vehicle?.licensePlateNumber)
        .replace('{{pickupdate}}', dayjs(res[0].data?.pickUpDate ? res[0].data?.pickUpDate : res[0].data?.booking?.endDate).format('DD/MM/YYYY HH:MM a'))
        .replace('{{message}}', '')
        .replace('{{orgname}}', res[0].data.organizationDetail?.nameOfOrganization)
        .replace('{{orgcontactnumber}}', res[0].data.organizationDetail?.phoneNumber)
        .replace('{{claimnumber}}', res[0].data.customer?.claimNumber);
    }else if(channel?.toLowerCase() === 'whatsapp') {
      body = res[1].data.whatsAppBody        
        .replace('{{firstname}} {{lastname}}', this.organizationName)
        .replace('{{make}}', res[0].data.vehicle?.vehicleMake)
        .replace('{{model}}', res[0].data.vehicle?.vehicleModel)
        .replace('{{licensenumber}}', res[0].data.vehicle?.licensePlateNumber)
        .replace('{{pickupdate}}', dayjs(res[0].data?.pickUpDate ? res[0].data?.pickUpDate : res[0].data?.booking.endDate).format('DD/MM/YYYY HH:MM a'))
        .replace('{{message}}', '')
        .replace('{{orgname}}', res[0].data.organizationDetail?.nameOfOrganization)
        .replace('{{orgcontactnumber}}', res[0].data.organizationDetail?.phoneNumber)
        .replace('{{approvalurl}}', 'Approve/Reject');
      if(res[0].data.customer?.claimNumber) {
        body = body.replace('{{claimnumber}}', res[0].data.customer?.claimNumber);
      } else {
        body = body.replace('/{{claimnumber}}', '');
      }
    } else if(channel?.toLowerCase() === 'sms') {
      body = res[1].data.smsBody
        .replace('{{estimateurl}}', '')
        .replace('{{approvalurl}}', '');
    } 
    const dialogRef = this.dialog.open(RequestApprovalDialogComponent, {
      data: {
        title: title,
        email: this.organizationEmail,
        emailSubject: emailSubject,
        channel: channel?.toLowerCase(),
        body: body,
        code: this.organizationCountryCode,
        phone: this.organizationPhone

      },
      backdropClass: 'confirmation-popup-backdrop',
      maxHeight: '90vh',
      minWidth: '629px',
      width: '629px',
      autoFocus: false
    });

    dialogRef.afterClosed().subscribe({
      next: (response: any) => {
        if(response) {
          this.commonService.showLoading();
          this.messageService.sendApprovalMessage(response, this.repairGuid).subscribe({
            next: () => {
              const emailMessage = this.translationService.instant('request_approval_msg');
              this.commonService.showInfoToast(1000, emailMessage);
              this.commonService.hideLoading();
              this.getBookingRepairDetails();
            },
            error: () => {
              this.commonService.hideLoading();
            }
          })
        }
        
      }
    });
  }

  /**
   * valid statuses
   */
  get isValidCurrentStatus(): boolean {
    return [ 'readytoinvoice' ].includes(this.repairStatus.toLowerCase());
  }

  /**
   * show confirmation popup to navigate to gt estimate if all the estimate charges are 0
   */
  showConfirmationPopup(): void {
    const dialogRef = this.dialog.open(GtMotiveEstimationDialogComponent, {
      disableClose: true,
      data: {
        message: 'msg_estimate_zero'
      },
      backdropClass: 'confirmation-popup-backdrop',
      autoFocus: false
    });

    dialogRef.afterClosed().subscribe({
      next: (response: any) => {
        console.log(response);
        if (response) {
          this.redirectToGtEstimate();
        }
      }
    });
  }

  /**
   * get manual estimate
   */
  getManualEstimationDetail(message?: string): void {
    if (this.vehicleId && this.inspectionId) {
      this.monitorService.logEvent('getManualEstimationDetail', [ 'EstimateComponent', 'addenda-repair' ], {
        repairGuid: this.repairGuid,
        inspectionId: this.inspectionId,
        vehicleId: this.vehicleId
      });
      this.repairEstimateService.getRepairEstimate(this.repairGuid, this.inspectionId, this.vehicleId).subscribe({
        next: (response) => {
          this.showData = true;
          this.estimateResponse = response || {};
          this.i18n = this.commonService.geti18nInfo();
          //this.estimateResponse.currencyCode = this.i18n.currencySymbol;
          // this.estimateResponse.currencyCode = this.estimateResponse?.currencyCode || this.i18n.currencySymbol;
          // this.estimateSource = this.estimateResponse?.source || ''
          this.commonService.isRefreshRepairEstimateDetails.next(null)
          this.editEstimate = false
          this.commonService.hideLoading();
          this.toggleOptions()
          if (message) this.commonService.showInfoToast(5000, message)
        },
        error: (err: any) => {
          this.commonService.hideLoading();
          this.monitorService.logException(err, SeverityLevel.Error);
        }
      })
    } else {
      this.commonService.hideLoading();
      this.displayMissingInspectionMessage(this.repairGuid)
    }
  }

  /**
   * estimate
   */
  getEstimationDetail(msg: string): void {
    if (this.estimateSource === 'GTMotive') {
      this.getGTEstimationDetail();
    } else {
      this.getManualEstimationDetail();
    }
  }

  /**
   * get estimation detail
   */
  getGTEstimationDetail(): void {
    if (this.vehicleId && this.inspectionId) {
      this.monitorService.logEvent('getGTEstimationDetail', [ 'EstimateComponent', 'addenda-repair' ], {
        repairGuid: this.repairGuid,
        caseGuid: this.caseGuid,
        inspectionId: this.inspectionId,
        vehicleId: this.vehicleId
      });
      this.repairEstimateService.getEstimate(this.caseGuid, this.inspectionId, this.vehicleId, this.domainId).subscribe({
        next: (response) => {
          this.estimateResponse = response?.data || {};
          if (this.estimateResponse?.estimateCharges?.length == 0) {
            this.showConfirmationPopup();
            this.commonService.hideLoading();
            return;
          } else {
            this.showData = true;
          }
          this.i18n = this.commonService.getDefaulti18nInfo();
          //this.estimateResponse.currencyCode = this.i18n.currencySymbol;
          this.estimateResponse.currencyCode = this.estimateResponse?.currencyCode;
          this.editEstimate = false
          this.toggleOptions()
          this.commonService.hideLoading();
        },
        error: (err: any) => {
          this.commonService.hideLoading();
          this.monitorService.logException(err, SeverityLevel.Error);
        }
      });
    } else {
      this.commonService.hideLoading();
      this.displayMissingInspectionMessage(this.repairGuid)
    }
  }

  /**
    Retrieves the details of an estimation.
    @returns {void}
  */
  getEstimateFromCaseAndRepair(orgName: string, message: string): void {
    if (this.vehicleId && this.inspectionId) {
      this.monitorService.logEvent('getEstimateFromCaseAndRepair', [ 'EstimateComponent', 'addenda-repair' ], {
        repairGuid: this.repairGuid,
        inspectionId: this.inspectionId,
        vehicleId: this.vehicleId
      });
      const isUpdate = sessionStorage.getItem('update') || '0';
      if (this.route.snapshot.queryParams['send']) {
        const previousUrl = sessionStorage.getItem('current-url') || '';
        if (previousUrl.indexOf('mode=edit') != -1) {
          this.router.navigate(
            [],
            {
              relativeTo: this.route,
              queryParams: { mode: 'edit' },
              queryParamsHandling: 'merge'
            });
        }
      }

      if (isUpdate === '0') {
        this.getEstimationDetail(orgName);
      } else if (isUpdate === '1' && this.route.snapshot.queryParams['send'] && this.route.snapshot.queryParams['send'] == 'true') {
        this.repairEstimateService.readUpdateEstimate(this.caseGuid, this.inspectionId,
          this.vehicleId, 'put', this.domainId)
          .subscribe({
            next: () => {
              //get call
              sessionStorage.setItem('update', '0');

              this.getEstimationDetail(orgName);

              this.router.navigate(
                [],
                {
                  relativeTo: this.route,
                  queryParams: { send: 'false' },
                  queryParamsHandling: 'merge'
                });

              if (sessionStorage['initial-repair-types'] != '') {
                this.afterOperation = true;
                this.getBookingRepairDetails();
              }
              this.commonService.hideLoading();
            }
          });
      } else {
        this.getEstimationDetail(orgName);
      }
    } else {
      this.commonService.hideLoading();
      this.displayMissingInspectionMessage(this.repairGuid)
    }
  }

  /**
   * show repair type update message
   */
  showRepairTypeUpdateMessage(existingRepairTypes: any, initialRepairTypes: any[]): void {

    const currentRepairServiceTypes: any[] = existingRepairTypes;
    const currentRepairTypesArray = [];
    currentRepairServiceTypes?.forEach((serviceType) => {
      if (typeof serviceType == 'object') {
        currentRepairTypesArray.push(serviceType.repairServiceType);
      } else {
        currentRepairTypesArray.push(serviceType);
      }

    });

    if (currentRepairTypesArray.length > 0) {
      const newRepairTypes: any = currentRepairTypesArray.filter(q => !initialRepairTypes.includes(q));

      if (newRepairTypes.length > 0) {
        this.commonService.showNotification(`Repair Type was updated: Added ${newRepairTypes.join(',')}`);
        this.repairTypesArray = [ ...this.repairTypesArray, ...newRepairTypes ];
      }
    }
  }

  /**
    Displays a confirmation message to the user asking them to add their vehicle before adding an estimate.
    @param {string} bookingId - The ID of the booking to add an estimate for.
    @returns {void}
  */
  displayMissingInspectionMessage(bookingId: string): void {
    this.commonService.openAlertDialog('Notification', 'Please add your vehicle in order to add estimate.').afterClosed().subscribe({
      next: (result: boolean) => {
        if (result) {
          this.router.navigate([ `repair/booking/${bookingId}` ]);
        }
      },
      error: () => {
        this.commonService.hideLoading();
      }
    });
  }


  /**
    Retrieves the repair details for a given repair GUID.
    @returns {void}
  */
  getBookingRepairDetails(): void {
    this.commonService.showLoading();
    this.monitorService.logEvent('getBookingRepairDetails', [ 'EstimateComponent', 'addenda-repair' ], {
      repairGuid: this.repairGuid
    });
    this.bookingService.getRepairDetailByGuid(this.repairGuid).subscribe({
      next: async (result: any) => {
        if (result?.data) {
          this.commonService.repairDetails.next(result.data);
          this.commonService.bookingId.next(result.data.repairNumber);
          this.repairNumber = result.data.repairNumber;
          this.repairId = result.data.id;
          this.repairStatus = result.data.status;
          this.invoiceStatus = result.data.invoiceStatus;
          this.inspectionId = result.data.vehicle?.inspectionId;
          this.vehicleId = result.data.vehicle?.vehicleId
          this.caseGuid = result.data.quoteId;
          this.domainId = result.data.domainId;
          this.vehicleObj = result.data.vehicle;
          this.claimNumber = result.data?.customer?.claimNumber;
          this.estimateSource = result.data.estimateSource || '';
          this.organizationEmail = result.data?.organizationDetail?.partnerships ? result.data?.organizationDetail?.partnerships[0]?.email:'';          
          this.organizationPhone = result.data?.organizationDetail?.partnerships ? result.data?.organizationDetail?.partnerships[0]?.phoneNumber?.replace(result.data?.organizationDetail?.partnerships[0]?.countryCode, ''):'';
          this.organizationCountryCode = result.data?.organizationDetail?.partnerships ? result.data?.organizationDetail?.partnerships[0]?.countryCode:'';
          this.organizationName = result.data?.organizationDetail?.partnerships ? result.data?.organizationDetail?.partnerships[0]?.companyName:'';
          this.isRepairCreatedFromCase = result.data.isRepairCreatedFromCase;
          this.bookingId = result.data.booking?.bookingGuid;

          if(!this.vehicleId) {
            this.commonService.hideLoading();
            this.displayMissingInspectionMessage(this.repairGuid);
            return;
          }

          if (result.data.repairType?.toLowerCase() == 'repair') {
            if (!this.afterOperation) {
              this.getInitialRepairTypes(result.data.repairServiceTypes);
            } else {

              if (this.estimateSource != 'GTMotive') {
                this.showRepairTypeUpdateMessage(result.data.repairServiceTypes, this.repairTypesArray);

              } else {
                this.repairTypesArray = [];
                this.getInitialRepairTypes(result.data.repairServiceTypes);
                if(sessionStorage['initial-repair-types'] != '') {
                  this.showRepairTypeUpdateMessage(this.repairTypesArray, JSON.parse(sessionStorage['initial-repair-types']));
                }
                sessionStorage.setItem('initial-repair-types', '');
              }
            }
          }

          const communications = [];
          if (result.data?.booking?.communicationTypeId && result.data?.booking?.communicationTypeId.length > 0) {            
            result.data?.booking?.communicationTypeId.forEach((element) => {
              if(this.communicationChannels[element]) communications.push(this.communicationChannels[element]);
            });            
          }
          communications.sort((a, b) => a.label.localeCompare(b.label));
          this.communications = result.data?.booking?.communicationTypeId;

          this.dropdownApprovalModelFilter = { ...this.dropdownApprovalModelFilter, options: communications };

          if (result.data.customer && result.data.customer?.companyOrgType === 'Retail') {
            if (!result.data.customer?.isCompany) {
              this.organizationEmail = result.data.customer?.email;
              this.organizationPhone = result.data.customer?.mobileNumber;
              this.organizationCountryCode = result.data.customer?.countryCode;
              this.organizationName = `${result.data.customer?.firstName} ${result.data.customer?.lastName}`;
            }
          }

          // If Source does not exists then needs to check orgs packages if GT estimate is enabled or not
          // if yes redirect to GTEstimate else show create manual estimate options
          if (!this.estimateSource && this.checkGTEstimatePermission()) {
            this.redirectToGtEstimate();
          } else {
            this.getEstimateFromCaseAndRepair(result.data?.organizationDetail?.nameOfOrganization || '', '');
          }

          this.toggleOptions()
        } else {
          this.commonService.hideLoading();
        }
      },
      error: (err: any) => {
        this.commonService.hideLoading();
        this.monitorService.logException(err, SeverityLevel.Error);
      }
    });
  }

  /**
   * check gtEstimate permission
   */
  checkGTEstimatePermission(): boolean {
    const { orgPackages = [] } = this.userPermission;
    const repairEstimatePackage = orgPackages.filter((orgPackage: any) => {
      return orgPackage.automotiveServiceName === 'Repair Estimate';
    });

    if (repairEstimatePackage && repairEstimatePackage.length > 0) {
      const { permissions } = repairEstimatePackage[0];
      const gtEstimatePermission = permissions.filter((perm: any) => {
        return perm.permissionName === 'gt.estimate' || perm.permissionName === 'gt.estimate.vinquery';
      });
      if (gtEstimatePermission && gtEstimatePermission.length > 0) {
        return true;
      }
    }
    return false;
  }


  /**
    Lifecycle hook that is called after the component is initialized.
    It updates the page title and retrieves booking repair details.
  */
  ngOnInit(): void {
    this.monitorService.logEvent('ngOnInit', [ 'EstimateComponent', 'addenda-repair' ]);
    this.commonService.updatePage('/repair/customers/0', 'add-customer');
    this.repairGuid = this.route.parent?.snapshot.paramMap.get('id');
    this.getBookingRepairDetails();
    this.subscription = this.commonService.isRefreshRepairEstimateDetails.subscribe({
      next: (data: any) => {
        if(data) {
          if(this.estimateSource !== 'GTMotive' && this.vehicleId && this.inspectionId) {
            this.getManualEstimationDetail();
          }
        }
        
      }
    })
  }

  /**
  * tooggle options
  */
  toggleOptions(): void {
    if (this.dropdownModelFilter.options) {
      if (this.estimateSource !== 'GTMotive' && this.repairStatus?.toLowerCase() !== 'PendingApproval'.toLowerCase() && this.repairStatus?.toLowerCase() !== 'readytoinvoice') {
        const already = this.dropdownModelFilter.options?.find(ele => ele.value == 'addOperation')
        if (!already) {
          this.dropdownModelFilter = {
            ...this.dropdownModelFilter, options: [
              ...this.dropdownModelFilter.options, { 'label': this.translationService.instant('add_operation'), 'dropdownItemIcon': 'plus', 'value': 'addOperation' } ]
          }
        }
      } else {
        const alreadyIndex = this.dropdownModelFilter.options?.findIndex(ele => ele.value == 'addOperation');
        const options = [ ...this.dropdownModelFilter.options ];
        if (alreadyIndex !== -1) {
          options.splice(alreadyIndex, 1)
        }

        this.dropdownModelFilter = { ...this.dropdownModelFilter, options: [ ...options ] }
      }

      if (this.estimateResponse?.estimateCharges?.length > 0 && this.repairStatus?.toLowerCase() !== 'PendingApproval'.toLowerCase() && this.repairStatus?.toLowerCase() !== 'readytoinvoice') {
        const already = this.dropdownModelFilter.options?.find(ele => ele.value == 'editOperation')
        if (!already) {
          this.dropdownModelFilter = {
            ...this.dropdownModelFilter, options: [
              ...this.dropdownModelFilter.options, { 'label': this.translationService.instant('edit_estimate'), 'dropdownItemIcon': 'edit', 'value': 'editOperation' } ]
          }
        }
      } else {
        const alreadyIndex = this.dropdownModelFilter.options?.findIndex(ele => ele.value == 'editOperation');
        const options = [ ...this.dropdownModelFilter.options ];
        if (alreadyIndex !== -1) {
          options.splice(alreadyIndex, 1)
        }

        this.dropdownModelFilter = { ...this.dropdownModelFilter, options: [ ...options ] }
      }

      if (this.estimateResponse?.estimateCharges?.length > 0 && this.repairStatus?.toLowerCase() !== 'readytoinvoice') {
        const already = this.dropdownModelFilter.options?.find(ele => ele.value == 'requestStatus')
        if (!already) {
          this.dropdownModelFilter = {
            ...this.dropdownModelFilter, options: [
              ...this.dropdownModelFilter.options, { 'label': this.translationService.instant('self_approval'), 'dropdownItemIcon': 'clock-new', 'value': 'requestStatus' } ]
          }
        }
      } else {
        const alreadyIndex = this.dropdownModelFilter.options?.findIndex(ele => ele.value == 'requestStatus');
        const options = [ ...this.dropdownModelFilter.options ];
        if (alreadyIndex !== -1) {
          options.splice(alreadyIndex, 1)
        }

        this.dropdownModelFilter = { ...this.dropdownModelFilter, options: [ ...options ] }
      }
    }

  }

  /**
    changeCaseStatusDialog
  */
  changeCaseStatusDialog(model: any): void {
    if (model.value.value === 'addOperation') {
      this.openOperationPopup()
    } else if (model.value.value === 'editOperation') {
      this.handleEditEstimate()
    } else if (model.value.value === 'requestStatus') {
      this.handleRequestStatus()
    }
  }

  /**
    Opens a dialog box for the repair estimate operation component with the specified data and panel class.
    @returns {void}
  */
  openOperationPopup(): void {
    //return if the initial permission was manual and later change to GT
    if (this.checkGTEstimatePermission()) {
      this.commonService.showToast(0, 'msg_gt_permission');
      return;
    }

    const dialogRef = this.dialog.open(RepairEstimateOperationComponent, {
      data: {
        repairId: this.repairGuid
      },
      width: '85vw',
      height: '90vh',
      panelClass: 'operation-panel',
      autoFocus: false
    });

    dialogRef.afterClosed().subscribe({
      next: (response: any) => {
        if (response) {
          const statuses = [ 'draft', 'uploaddocuments', 'uploadphotos' ]
          if (statuses.includes(this.repairStatus?.toLowerCase())) {
            this.repairStatus = 'RepairEstimation';
          }
          this.afterOperation = true;

          this.getBookingRepairDetails();
        }
      }
    });
  }

  /**
    Sets the value of the editEstimate property to true.
    @returns {void}
  */
  handleEditEstimate(): void {
    if (this.estimateSource === 'GTMotive') {
      if (this.caseGuid && this.commonService.quotePermission) {
        this.commonService.showChangeWarningMessage(this.redirectToGtEstimate.bind(this), Modules.quote);
      } else {
        this.redirectToGtEstimate();
      }
      //this.redirectToGtEstimate();
      return;
    } else {

      //return if the initial permission was manual and later change to GT
      if (this.checkGTEstimatePermission()) {
        this.commonService.showToast(0, 'msg_gt_permission');
        return;
      }
      //
      this.editEstimate = true;
    }
  }

  /**
    Handles the saving of an edited estimate.
    Sets the editEstimate property to false.
  */
  handleSaveEditedEstimate(): void {
    this.editEstimate = false;
  }

  /**
    Asynchronously handles the editing of a repair operation.
    @async
    @function handleEditRepairOperation
    @param {string} repairOperationGuid - The GUID of the repair operation to be edited.
    @returns {Promise} - A promise that resolves when the operation is complete.
    @throws {Error} - If an error occurs during the operation.
  */
  async handleEditRepairOperation(repairOperationGuid: string): Promise<void> {
    try {
      this.commonService.showLoading();
      this.monitorService.logEvent('getRepairEstimateOperationDetails', [ 'EstimateComponent', 'addenda-repair' ]);
      const operationDeatils = await this.getRepairEstimateOperationDetails(repairOperationGuid);
      this.commonService.hideLoading();
      if (operationDeatils?.data) {
        // Convert the keys to so first letter is uppercase
        const entries = Object.entries(operationDeatils.data);
        const capsEntries = entries.map(entry => [ entry[0][0].toUpperCase() + entry[0].slice(1), entry[1] ]);
        const capsPopulations = Object.fromEntries(capsEntries);
        const dialogRef = this.dialog.open(RepairEstimateOperationComponent, {
          data: {
            repairId: this.repairGuid,
            operationDetails: capsPopulations
          },
          width: '85vw',
          height: '90vh',
          panelClass: 'operation-panel',
          autoFocus: false
        });

        dialogRef.afterClosed().subscribe({
          next: (response: any) => {
            if (response) {
              this.getEstimateFromCaseAndRepair('', '')
            }
          }
        });
      }
    } catch (error) {
      this.commonService.hideLoading();
      this.monitorService.logException(error, SeverityLevel.Error);
    }
  }

  /**
    Retrieves details of a repair estimate operation with the given GUID.
    @async
    @param {string} repairOperationGuid - The GUID of the repair estimate operation to retrieve details for.
    @returns {Promise} - A Promise that resolves with the details of the repair estimate operation.
  */
  public async getRepairEstimateOperationDetails(repairOperationGuid: string): Promise<any> {
    return firstValueFrom(this.repairEstimateOperationService.getRepairEstimateOperationDetails(repairOperationGuid))
  }

  /**
    Handles the click event on an icon.
    @param {object} output - The output object containing the ID and action of the icon.
  */
  handleIconClicked(output): void {
    const { id, action } = output
    if (action === 'edit') this.handleEditRepairOperation(id)

    if (action === 'delete') {
      if (this.caseGuid) {
        this.commonService.showChangeWarningMessage(this.handleDeleteRepairOperation.bind(this, id), Modules.quote);
      } else {
        this.handleDeleteRepairOperation(id);
      }
    }
  }

  /**
    Deletes a repair operation with the given ID.
    @param {string} id - The ID of the repair operation to be deleted.
    @returns {void}
  */
  handleDeleteRepairOperation(id: string): void {
    this.commonService.openDeleteDialog()
      .afterClosed().subscribe((data) => {
        if (data) {
          this.monitorService.logEvent('getBookingRepairDetails', [ 'EstimateComponent', 'addenda-repair' ], {
            repairGuid: this.repairGuid,
            id: id
          });
          this.commonService.showLoading();
          firstValueFrom(this.repairEstimateOperationService.deleteRepairEstimateOperation(this.repairGuid, id)).then((response) => {
            let deleteTextMessage = '';
            if (response?.message) deleteTextMessage = response.message;
            this.getManualEstimationDetail(deleteTextMessage);
          }).catch(() => {
            this.commonService.hideLoading();
          });
        }
      });
  }

  /**
   * downloadPdf()
  */
  public downloadPdf(): void {
    this.commonService.showLoading();
    const apiCall = 'getXADownloadUrl';
    this.monitorService.logEvent('downloadPdf', [ 'RepairEstimateComponent', 'addenda-repair', {
      caseId: this.caseGuid
    } ]);
    this.repairEstimateService[apiCall](this.caseGuid).subscribe({
      next: (result: any) => {
        window.open(result.data.url, '_top');
        this.commonService.hideLoading();
      },
      error: () => {
        this.commonService.hideLoading();
      }
    })
  }

  /**
   *
   * @param repairServiceTypes
   */
  private getInitialRepairTypes(repairServiceTypes: any): void {
    repairServiceTypes.forEach((serviceType) => {
      this.repairTypesArray.push(serviceType.repairServiceType);
    });
  }

  /**
   * Cancel booking
   */
  cancelBooking(): void {
    this.commonService.openDeleteDialog()
      .afterClosed().subscribe((data) => {
        if (data) {
          this.commonService.showLoading();
          this.calendarService.deleteBooking(this.bookingId).subscribe({
            next: () => {
              this.commonService.hideLoading();
              this.router.navigate([ '/repair/booking-journal' ]);
            }
          });
        }
      });
  }

  /**
   * handle request status
   */
  handleRequestStatus(): void {
    //todo
    const dialogRef = this.dialog.open(EstimateApprovalComponent, {
      width: '629px',
      maxWidth: '629px',
      autoFocus: false, 
      data: this.repairGuid
    });

    dialogRef.afterClosed().subscribe({
      next: (res: any) => {
        //todo
        if(res) {
          this.getBookingRepairDetails();
        }
      } 
    });
  }

  /**
   * on destroy
   */
  ngOnDestroy(): void {
    this.commonService.accessRight.unsubscribe();
    this.subscription.unsubscribe();
    this.monitorService.logEvent('ngOnDestroy', [ 'EstimateComponent', 'addenda-repair' ]);
  }

  /**
   * check if repair is invoiced or not
   * @returns 
   */
  repairNotInvoiced(): boolean {
    if(this.repairStatus?.toLowerCase() === 'invoiced'
    || this.repairStatus?.toLowerCase() === 'paid'
    || this.repairStatus?.toLowerCase() === 'closed'
    || this.repairStatus?.toLowerCase() === 'complete'
    || this.repairStatus?.toLowerCase() === 'readytoinvoice'
    || this.invoiceStatus?.toLowerCase() === 'readytoinvoice'
    || this.invoiceStatus?.toLowerCase() === 'invoiced') {
      return false;
    }
    return true;
  }

}
