import { Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { TranslateService } from '@ngx-translate/core';
import { CommentDialogComponent } from 'src/app/dialogs/comment-dialog/comment-dialog.component';
import { NotifyDialogComponent } from 'src/app/dialogs/notify-dialog/notify-dialog.component';
import { ChqWidgetsButtonModel } from 'src/app/model/chq-widgets-button-model';
import { RepairJobTimerStatus, RepairTaskStatus } from 'src/app/model/chq-widgets-input-model';
import { BookingService } from 'src/app/services/booking/booking.service';
import { CommonService } from 'src/app/services/common/common.service';
import { MonitorService } from 'src/app/services/monitor-service/monitor.service';

@Component({
  selector: 'repair-stages-table',
  templateUrl: './repair-stages-table.component.html',
  styleUrls: [ './repair-stages-table.component.scss' ]
})
export class RepairStagesTableComponent implements OnInit, OnChanges {

  @Input() repairDataMappings: any;
  @Input() notifyData: any;
  @Input() activateRepairButton: boolean
  @Input() activateNotifyButton: boolean
  @Input() repairStatus: string;
  @Output() updateJobs: EventEmitter<any> = new EventEmitter();
  @Output() confirmJob: EventEmitter<any> = new EventEmitter();
  public taskList: any[] = [];
  public mandatoryTasks: number = 0;
  public allMandatoryTasks:number = 0;

  public notifyButtonModel: ChqWidgetsButtonModel = {
    label: 'notify',
    type: 'outline',
    icon: 'send',
    onclick: this.openNotify.bind(this)
  }

  public addPartButtonModel: ChqWidgetsButtonModel = {
    label: 'Complete Repair',
    type: 'disabled',
    onclick: this.completeJob.bind(this)
  }


  /**
   * 
   */
  openNotify(): void {
    const dialogRef = this.dialog.open(NotifyDialogComponent, {
      data: {
        repairGuid: this.notifyData.repairGuid,
        domainId: this.notifyData.domainId,
        templateId: 1,
        communicationList: this.notifyData.communicationList,
        communicatedList: this.notifyData.communicatedList,
        method: 'notifier'
      },
      backdropClass: 'confirmation-popup-backdrop',
      maxHeight: '693px',
      maxWidth: '629px'
    });

    dialogRef.afterClosed().subscribe({
      next: (response: any) => {
        if (response?.notifySend) {
          console.log(response);
          if (response.selectedCommunication) {
            response.selectedCommunication.forEach((element) => {
              this.notifyData.communicatedList.push({ id: 0, eventName: response.templateKey, communicationTypeId: element });
            });
          }
          this.commonService.showInfoToast(1000, 'Message sent');
        }
      }
    });

  }

  /**
   * complete jon
   */
  completeJob(): void {
    this.confirmJob.emit();
  }

  /**
   * constructor
   */
  constructor(
    private monitorService: MonitorService,
    public dialog: MatDialog,
    private bookingService: BookingService,
    private translateService: TranslateService,
    public commonService: CommonService,
  ) {

  }

  /**
   * check status of job
   */
  ngOnChanges(changes: SimpleChanges): void {
    this.monitorService.logEvent('ngOnChanges', [ 'ChqEstimateDetailsComponent', 'addenda-repair' ]);
    if (changes) {
      if(this.activateNotifyButton && changes['repairStatus'] && changes['repairStatus'].currentValue){
        this.activateNotifyButton = changes['repairStatus'].currentValue.toLowerCase() === 'repairinprogress';
      }
      if (changes['repairDataMappings'] && changes['repairDataMappings'].currentValue) {
        this.taskList = [ ...this.repairDataMappings.repairJobTasks ];
        this.taskList.forEach((jb) => {
          const repairTypesArr = jb.repairJobMappings.map((jbk) => {
            return jbk.repairServiceTypeName;
          });
          jb.repairTypeNames = repairTypesArr.join(', ');
        });
        this.mandatoryTasks = this.taskList.filter((task) => {
          return task.isMandatory === true && task.statusName !== 'Completed';
        }).length;

        this.allMandatoryTasks = this.taskList.filter((task) => {
          return task.isMandatory === true;
        }).length;
      }
      if (changes['activateRepairButton'] && changes['activateRepairButton'].currentValue) {
        this.addPartButtonModel = { ...this.addPartButtonModel, type: 'primary' };
      }
      const statuses = [ 'readytoinvoice', 'invoiced', 'paid', 'complete' ];
      if (statuses.includes(this.repairStatus?.toLowerCase()) && this.activateRepairButton) {
        this.addPartButtonModel = { ...this.addPartButtonModel, label: 'go_invoice' }
      }
      setTimeout(() => {
        this.mandatoryTasks = this.taskList.filter((task) => {
          return task.isMandatory === true && task.statusName !== 'Completed';
        }).length;
      })

    }
  }

  /**
   * init
   */
  ngOnInit(): void {
    if (this.repairDataMappings) {
      //this.repairDataMappings.repairJobMappings.forEach((mapping) => {
      //if (mapping.repairTasks && mapping.repairTasks.length > 0)
      //this.taskList = [ ...this.taskList, ...mapping.repairTasks ];
      //});
      this.taskList = [ ...this.repairDataMappings.repairJobTasks ];
      this.taskList.forEach((jb) => {
        const repairTypesArr = jb.repairJobMappings.map((jbk) => {
          return jbk.repairServiceTypeName;
        });
        jb.repairTypeNames = repairTypesArr.join(', ');
      });
      this.mandatoryTasks = this.taskList.filter((task) => {
        return task.isMandatory === true && task.statusName !== 'Completed';
      }).length;

      this.allMandatoryTasks = this.taskList.filter((task) => {
        return task.isMandatory === true;
      }).length;

    }

    if (this.repairStatus?.toLowerCase() === 'readytoinvoice' && this.activateRepairButton) {
      this.addPartButtonModel = { ...this.addPartButtonModel, label: 'go_invoice' }
    }
  }

  /**
   * updateTaskStatus
   */
  updateTaskStatus(currentJob: any): void {
    if(this.commonService.isViewOnly){
      return;
    }
    if (!this.repairDataMappings.startDate) {
      this.commonService.openAlertDialog(this.translateService.instant('warning'), this.translateService.instant('warning_comment')).afterClosed().subscribe();
      return;
    }

    if (this.repairDataMappings?.timerLog?.statusName === RepairJobTimerStatus.Pause) {
      this.commonService.openAlertDialog(this.translateService.instant('warning'), this.translateService.instant('warning_pause_comment')).afterClosed().subscribe();
      return;
    }

    if (!this.repairDataMappings.startDate) {
      this.commonService.openAlertDialog(this.translateService.instant('warning'), this.translateService.instant('warning_comment')).afterClosed().subscribe();
      return;
    }

    if (this.repairDataMappings.endDate) {
      this.commonService.openAlertDialog(this.translateService.instant('cannot_update_status'), this.translateService.instant('job_completed')).afterClosed().subscribe();
      return;
    }

    if (this.mandatoryTasks <= 1 && currentJob.isMandatory) {
      const dialogRef = this.commonService.openConfirmationOkDialog(this.translateService.instant('confirm_action'), this.translateService.instant('job_no_mandatory_task'));
      dialogRef.afterClosed().subscribe((action: boolean) => {
        if (action) {
          currentJob.isLoading = true;
          this.bookingService.updateRepairTaskStatus(currentJob.repairTaskGuid, this.repairDataMappings.repairJobGuid,
            currentJob.completedDate ? RepairTaskStatus.NotStarted :RepairTaskStatus.Completed).subscribe({
            next: () => {
              if(currentJob.completedDate){
                currentJob.completedDate = null;
              }else{
                currentJob.completedDate = new Date();
              }
              this.mandatoryTasks--;
              this.updateJobs.emit();
              currentJob.isLoading = false;
            }, error: () => {
              currentJob.isLoading = false;
            }
          });
        } else {
          currentJob.isLoading = false;
        }
      });
      return;
    }

    currentJob.isLoading = true;
    this.bookingService.updateRepairTaskStatus(currentJob.repairTaskGuid, this.repairDataMappings.repairJobGuid,
      currentJob.completedDate ? RepairTaskStatus.NotStarted :RepairTaskStatus.Completed).subscribe({
      next: () => {
        if(currentJob.completedDate){
          currentJob.completedDate = null;
        }else{
          currentJob.completedDate = new Date();
        }
        
        this.updateJobs.emit();
        currentJob.isLoading = false;
      }, error: () => {
        currentJob.isLoading = false;
      }
    });
  }

  /**
   * insert/update comment
   */
  showCommentDialog(currentNote: string, currentJob: any): void {
    let note = '';
    if (currentNote) {
      note = currentNote;
    }

    this.monitorService.logEvent('showCommentDialog', [ 'CommentDialogComponent', 'addenda-repair' ]);
    this.dialog.open(CommentDialogComponent, {
      data: {
        notes: note
      },
      panelClass: 'comment-modalbox',
      autoFocus: false
    }).afterClosed().subscribe({
      next: (data: any) => {
        if (!data) {
          return;
        }
        if (data.delete) {
          data.comment = '';
        }
        currentJob.isNoteLoading = true;
        this.bookingService.updateRepairTaskNotes(this.repairDataMappings.repairJobGuid, data.comment, currentJob.repairTaskGuid).subscribe({
          next: () => {
            currentJob.notes = data.comment;
            this.updateJobs.emit();
            currentJob.isNoteLoading = false;
          }, error: () => {
            currentJob.isNoteLoading = false;
          }
        });
      }
    });
  }
}
