import { KeyValue } from '@angular/common';
import { Component, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { CommonService, i18nInfo } from 'src/app/services/common/common.service';

@Component({
  selector: 'app-invoice-summary',
  templateUrl: './invoice-summary.component.html',
  styleUrls: [ './invoice-summary.component.scss' ],
})
export class InvoiceSummaryComponent {
  /**
    @returns {void} constructor for InvoiceSummaryComponent
  */
  constructor(
    @Inject(MAT_DIALOG_DATA) public summaryDialogData:any,
    private commonService:CommonService,
    public dialogRef: MatDialogRef<InvoiceSummaryComponent>
  ) {
    this.i18n = this.commonService.geti18nInfo();
    this.digitInfo = this.commonService.getDigitsInfo()
    this.locale = this.commonService.getLocale()
    this.currencySymbol = this.commonService.getCurrencySymbol()
  }
  i18n: i18nInfo;
  digitInfo = ''
  locale = '';
  currencySymbol = '';

  /**  
    Order by 
  */
  valueAscOrder(a: KeyValue<string, string>, b: KeyValue<string, string>): number{
    const sortOrder = { 'part': '1', 'labour': '2', 'paint': '3' }
    return sortOrder[a.key?.toLowerCase()].localeCompare(sortOrder[b.key?.toLowerCase()]);
  }

  /**
    @returns {void} function to close Summary popup
  */
  onClose(): void {
    this.dialogRef.close();
  }
}
