  /**
    @returns {void} Open Invoice Summary popup
  */
  goToGenerateInvoice(): void {
    if(this.status?.toLowerCase() === 'readytoinvoice'){
      let formType = 1;
      if(this.currentWorkProvider?.toLowerCase() === 'retail'){
        if(this.currentWorkProviderType?.toLowerCase() === 'person'){
          formType = 2;
        }
      }
      this.router.navigate([ '../invoicesEdit' ], {
        relativeTo: this.route,
        queryParams: { formType },
      });
      return;
    }
    const dialogRef = this.commonService.openConfirmYesNoDialog(
      'Confirm to Continue',
      'Once the invoice is generated, you won’t be able to edit the estimate again. Are you sure you want to continue?'
    );
    dialogRef.afterClosed().subscribe((res: boolean) => {
      if (res) {
        this.updateEstimateCharge(true);
      }
    });
  }

  orgImageLoading: boolean = true;
  orgLogoUrl: string = '';
  orgName: string = '';
  public paymentStatus:string = '';

  isAllExpanded = true;
  isPartsExpanded = true;
  isLabourExpanded = true;
  isPaintExpanded = true;
