import { Observable } from 'rxjs';
import { ValidationModel } from './chq-validation-model';
import { FormControl } from '@angular/forms';

export interface IControlValue {
  name: string;
  value: any;
  keyFieldValue?: any;
  type?: string,
  validationMessage?: string,
  isValid?: boolean
}

export interface ChqWidgetsInputModel<T = any> {
  placeHolder: string;
  label: string;
  value: string | number | boolean | Date | Array<string>;
  type: string;
  options?: any[];
  displayValue?: string;
  fieldValue?: string;
  selectedOption?: any;
  idField?: any;
  mode?: string;
  isMultiSelect?: boolean,
  showSelectedOption?: boolean,
  name: string;
  observable?: Observable<T>,
  validation?: ValidationModel,
  icon?: string;
  preIcon?: string;
  iconColor?: string;
  maxLength?: number;
  minDate?: Date;
  maxDate?: Date;
  setUntouched?: boolean;
  showImageFlag?: boolean;
  errorWhenEmpty?: boolean;
  required?: boolean;
  customMessage?: string;
  onObservableLoad?: (item: any) => void;
  formControl?: FormControl,
  onIconClick?:(val:any)=>void;
  disabled?: boolean,
  textModeIcon?: string
  suffix?: string
  interval?: number,
  hideLabel?:boolean,
  pattern?: RegExp;
  cssClass?: string;
  rows?: number;
}

export enum RepairJobTimerStatus
{
    Start = 'Start',
    Pause = 'Pause',
    Stop = 'Stop'
}

export enum RepairTaskStatus
{
    NotStarted = 'NotStarted',
    Completed = 'Completed'
}