import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { CommonService } from 'src/app/services/common/common.service';
import { MonitorService } from 'src/app/services/monitor-service/monitor.service';

@Component({
  selector: 'app-add-photos',
  templateUrl: './add-photos.component.html',
  styleUrls: [ './add-photos.component.scss' ]
})
export class AddPhotosComponent {
  header = {
    title: 'Upload Images',
    step: 0,
    description: '',
    stepName: 'Back / Bulk Upload',
    rightActionText: 'Delete all and Start Over',
    rightActionIcon: 'delete',
    isRepairModule: true,
    showTitle: true
  };

  /**
 * constructor
 * @param router 
 */
  constructor(private commonService: CommonService, private router: Router,
    private monitorService:MonitorService) {
    /*if (this.commonService.accessRight.closed) {
      this.commonService.accessRight = new BehaviorSubject<boolean>(true);

    }*/
  }

  /**
   * update title according to option selected
   * @param $event 
   */
  handleUpdateMode($event):void{
    if($event.mode === 'error'){
      this.displayMissingInspectionMessage($event.id);
    }else
    if($event.mode === 'single'){
      this.header = { ...this.header, title: 'back_smart_photo' }
    }else{
      this.header = { ...this.header, title: 'back_bulk_upload' }
    }
  }

  /**
   * show error dialog
   */
  displayMissingInspectionMessage(bookingId:string):void{
    this.commonService.openConfirmationOkDialog('Notification', 'Please add your vehicle in order to add car images.').afterClosed().subscribe({
      next: (result:boolean)=>{
        if(result){
          this.router.navigate([ `repair/booking/${bookingId}` ]);
        }
      },
      error: ()=>{
        this.commonService.hideLoading();
      }
    });
  }


  /**
   * destroy
   */
  ngOnDestroy(): void {
    this.commonService.accessRight.unsubscribe();
    this.monitorService.logEvent('ngOnDestroy', [ 'Photos component', 'addenda-repair' ]);
  }
}
