  /**
   * on dropdown observable load
   */
  onObservableLoad(items: any): void {
    this.commonService.hideLoading();

    if (!items) {
      return;
    }
    if (items && items.length > 0) {

      if(!this.dataResponse.contactNumber){
        // Comment out below code to fix bug: https://dev.azure.com/XASystems/XA%20APPs/_workitems/edit/21550
        // const defaultCode = '+91';
        // const defaultSelectedItem = items.filter((it: any) => {
        //   return it.phoneCountryCode === defaultCode;
        // });
        // const output = defaultSelectedItem[0];
        // this.dataResponse.code = output['phoneCountryCode'];
        // const validationPattern = output['regExpression'];
        this.phoneCode = { ...this.phoneCode, selectedOption: {} };
        this.contactNumberInput = { ...this.contactNumberInput, value: null };
        // if (this.phone) {
        //   this.phone.updateCurrentValidation(validationPattern, true, true);
        // }
        return;
      }

      const code = this.dataResponse.contactNumber?.substring(0, 3);
      const code1 = this.dataResponse.contactNumber?.substring(0, 4);
      let currentPhoneNumber = '';
      let selectedItem = items.filter((it: any) => {
        return it.phoneCountryCode === code;
      });
      if(selectedItem.length === 0 ){
        currentPhoneNumber = this.dataResponse.contactNumber?.substring(4, this.dataResponse.contactNumber?.length);
        selectedItem = items.filter((it: any) => {
          return it.phoneCountryCode === code1;
        });
      }else{
        currentPhoneNumber = this.dataResponse.contactNumber?.substring(3, this.dataResponse.contactNumber?.length);
      }

      if (selectedItem.length > 0) {
        const output = selectedItem[0];
        const validationPattern = output['regExpression'];
        this.phoneCode = { ...this.phoneCode, selectedOption: output, mode: this.repairStatus.toLowerCase()==='complete' || this.commonService.isViewOnly || this.repairStatus.toLowerCase()==='paid'?'text':null };
        this.dataResponse.code = output['phoneCountryCode'];
        this.contactNumberInput = { ...this.contactNumberInput, value: currentPhoneNumber };
        if (this.phone) {
          this.phone.updateCurrentValidation(validationPattern, true, true);
          this.phone.setDirty();
        }
      }
    }
  }

