import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { SeverityLevel } from '@microsoft/applicationinsights-web';
import { catchError, firstValueFrom, forkJoin, of } from 'rxjs';
import { BehaviorSubject } from 'rxjs/internal/BehaviorSubject';
import { RepairEstimateOperationComponent } from 'src/app/dialogs/repair-estimate-operation/repair-estimate-operation.component';
import { ChqWidgetsButtonModel } from 'src/app/model/chq-widgets-button-model';
import { ChqWidgetsInputModel, IControlValue } from 'src/app/model/chq-widgets-input-model';
import { BookingService } from 'src/app/services/booking/booking.service';
import { CommonService, i18nInfo } from 'src/app/services/common/common.service';
import { MonitorService } from 'src/app/services/monitor-service/monitor.service';
import { RepairEstimateOperationService } from 'src/app/services/repair-estimate-operation/repair-estimate-operation.service';
import { RepairEstimateService } from 'src/app/services/repair-estimate/repair-estimate.service';

@Component({
  selector: 'app-repair-parts',
  templateUrl: './repair-parts.component.html',
  styleUrls: [ './repair-parts.component.scss' ]
})

export class RepairPartsComponent implements OnInit {
  public notifyServiceButtonModel: ChqWidgetsButtonModel = {
    label: 'Notify Service Advisor',
    type: 'outline',
    icon: 'paperplane',
    iconColor: 'black'
  }

  public addPartButtonModel: ChqWidgetsButtonModel = {
    label: 'Add Part',
    type: 'outline',
    icon: 'plus',
    onclick: this.openPartPopup.bind(this)
  }

  public saveButtonModel: ChqWidgetsButtonModel = {
    label: 'Save',
    type: 'disabled',
    onclick: this.saveParts.bind(this)
  }

  public individualStatusModel: ChqWidgetsInputModel = {
    placeHolder: 'Select Status',
    name: 'indStatus',
    label: '',
    type: 'select',
    value: 0,
    displayValue: 'name',
    options: []
  }

  public individualStatusModels: ChqWidgetsInputModel[] = [];

  public parts: any = [];
  public repairResponse: any;
  public estimateResponse: any;
  public partsStatus: string = '';

  public repairGuid: string = '';
  private estimateSource: string = '';

  private userPermission: any = {};
  public partMapper: any = {};
  public editMode: any = [];

  public formData: any[] = [];
  public repairStatus: string = '';
  public readOnlyMode = false;
  public hideButton = false;

  private repairTypesArray: Array<string> = [];
  private afterOperation: boolean = false;

  i18n: i18nInfo;

  /**
   * show delete
   */
  get disableEdit(): boolean {
    const statusList = [ 'pendingapproval', 'readytoinvoice', 'invoiced', 'complete' ];

    if (statusList.includes(this.repairStatus?.toLowerCase())) {
      return true;
    }
    return false;
  }

  /**
   * constructor
   */
  constructor(public dialog: MatDialog,
    private bookingService: BookingService,
    public commonService: CommonService,
    private route: ActivatedRoute,
    private monitorService: MonitorService,
    private repairEstimateService: RepairEstimateService,
    private repairEstimateOperationService: RepairEstimateOperationService,
    private router: Router) {

    if (this.commonService.accessRight.closed) {
      this.commonService.accessRight = new BehaviorSubject<boolean>(true);
    }
    this.commonService.userProfileData.subscribe((res) => {
      if (res && res.data) {
        const { userPermission } = res.data;
        this.userPermission = userPermission || {};
      }
    });

    this.i18n = this.commonService.geti18nInfo();
    this.route.parent?.params?.subscribe((data) => {
      if (this.repairGuid != '' && this.repairGuid !== data['id']) {
        this.repairGuid = data['id'];
        this.getRepairDetails();
        this.getParts();
      }
    });
  }

  /**
   * ngOninit
   */
  ngOnInit(): void {
    this.repairGuid = this.route.parent?.snapshot.paramMap.get('id');
    this.commonService.showLoading();
    const detailResult = forkJoin({
      partStatuses: this.repairEstimateOperationService.getPartStatuses(),
      part: this.repairEstimateOperationService.getParts(this.repairGuid),
      repairDetail: this.bookingService.getRepairDetailByGuid(this.repairGuid)
    }).pipe(
      catchError(error => of(error))
    )
    detailResult.subscribe({
      next: (resultMap: any) => {
        if (resultMap.partStatuses) {
          this.getPartStatuses(resultMap.partStatuses?.data)
        }

        if (resultMap.part) {
          const response = resultMap.part;
          if (response) {
            this.parts = response.repairParts;
            if (this.parts?.length > 0) {
              this.parts.forEach((part: any, index: number) => {
                this.individualStatusModels[index] = this.individualStatusModel;
                this.individualStatusModels[index] = {
                  ...this.individualStatusModels[index],
                  selectedOption: { 'name': part.status, 'id': part.statusId }, name: `ind-status-${index}`
                };
                this.editMode[index] = false;
              });
            }
            this.partsStatus = this.mapStatus(response.status);

          }
        }
        if (resultMap.repairDetail) {
          const res = resultMap.repairDetail;
          this.repairResponse = resultMap.repairDetail.data;
          this.commonService.bookingId.next(this.repairResponse.repairNumber);
          this.commonService.repairGuid.next(this.repairResponse.repairGuid);
          this.commonService.repairDetails.next(this.repairResponse);
          this.estimateSource = this.repairResponse.estimateSource || '';
          this.repairStatus = this.repairResponse.status;

          if (this.disableEdit) {
            // this.addPartButtonModel.type = 'disabled-outline';
            // this.saveButtonModel.type = 'disabled-outline';
            this.readOnlyMode = true;
            this.hideButton = true;
          } else {
            if ((this.estimateSource != 'GTMotive' && this.checkGTEstimatePermission())
              || (this.estimateSource == 'GTMotive' && !this.checkGTEstimatePermission())) {
              this.readOnlyMode = true;
            }
            // else if (this.repairStatus == 'PendingApproval') {
            //   this.addPartButtonModel.type = 'disabled-outline';
            //   this.saveButtonModel.type = 'disabled-outline';
            //   this.readOnlyMode = true;
            // }
          }

          if (res.data.repairType?.toLowerCase() == 'repair') {
            if (!this.afterOperation) {
              this.getInitialRepairTypes(res.data.repairServiceTypes);
            }

          }
        }
        //this.commonService.hideLoading();
      }, error: (err: any) => {
        this.commonService.hideLoading();
      }
    })

  }

  /**
   * get part statuses 
   */
  getPartStatuses(data): void {
    if (data) {
      this.individualStatusModel = { ...this.individualStatusModel, options: data }
      const partStatuses: any = {};
      this.individualStatusModel.options.forEach((item, i) => {
        this.individualStatusModel.options[i] = { ...this.individualStatusModel.options[i], dropdownItemIcon: this.getIcon(item.name) };
        partStatuses[item.id + '_name'] = item.description;
        partStatuses[item.id + '_icon'] = this.getIcon(item.name);
      });
      this.partMapper = partStatuses;
    }
  }

  /**
   * get icon
   */
  getIcon(item: string): string {
    switch (item) {
    case 'NotOrdered':
      return 'parts-not-ordered';
    case 'Ordered':
      return 'parts-ordered';
    case 'Arrived':
      return 'parts-arrived';
    default:
      return '';
    }
  }

  /**
   * map status
   * @param status 
   * @returns status string
   */
  mapStatus(status: string): string {
    switch (status) {
    case 'NotOrdered':
      return 'Not Ordered';
    case 'OrderedPartially':
      return 'Ordered Partially';
    case 'ArrivedPartially':
      return 'Arrived Partially';
    default:
      return status;
    }
  }

  /**
   * get parts
   */
  getParts(): void {

    this.commonService.showLoading();
    this.repairEstimateOperationService.getParts(this.repairGuid).subscribe({
      next: (response: any) => {
        if (response) {
          this.parts = response.repairParts;
          if (this.parts?.length > 0) {
            this.parts.forEach((part: any, index: number) => {
              this.individualStatusModels[index] = this.individualStatusModel;
              this.individualStatusModels[index] = {
                ...this.individualStatusModels[index],
                selectedOption: { 'name': part.status, 'id': part.statusId }, name: `ind-status-${index}`
              };
              this.editMode[index] = false;
            });
          }
          this.partsStatus = this.mapStatus(response.status);

        }
      },
      complete: () => {
        this.commonService.hideLoading();
      },
      error: () => {
        this.commonService.hideLoading();
      }
    });
  }

  /**
   * save parts
   */
  saveParts(): void {
    this.commonService.showLoading();
    this.repairEstimateOperationService.editPartStatuses(this.formData, this.repairGuid).subscribe({
      next: (response: any) => {
        if (response) {
          this.getParts();
          this.formData = [];
          this.saveButtonModel.type = 'disabled';
        }
      },
      complete: () => {
        this.commonService.hideLoading();
      }
    })
  }

  /**
   * get repair details
   */
  getRepairDetails(): void {
    this.commonService.showLoading();
    this.bookingService.getRepairDetailByGuid(this.repairGuid).subscribe({
      next: (result: any) => {
        this.repairResponse = result.data;
        this.commonService.bookingId.next(this.repairResponse.repairNumber);
        this.commonService.repairGuid.next(this.repairResponse.repairGuid);
        this.commonService.repairDetails.next(this.repairResponse);
        this.estimateSource = this.repairResponse.estimateSource || '';
        this.repairStatus = this.repairResponse.status;

        if (this.disableEdit) {
          // this.addPartButtonModel.type = 'disabled-outline';
          // this.saveButtonModel.type = 'disabled-outline';
          this.readOnlyMode = true;
          this.hideButton = true;
        } else {
          if ((this.estimateSource != 'GTMotive' && this.checkGTEstimatePermission())
            || (this.estimateSource == 'GTMotive' && !this.checkGTEstimatePermission())) {
            this.readOnlyMode = true;
          }
          // else if (this.repairStatus == 'PendingApproval') {
          //   this.addPartButtonModel.type = 'disabled-outline';
          //   this.saveButtonModel.type = 'disabled-outline';
          //   this.readOnlyMode = true;
          // }
        }

        if (result.data.repairType?.toLowerCase() == 'repair') {
          if (!this.afterOperation) {
            this.getInitialRepairTypes(result.data.repairServiceTypes);
          } else {

            if (this.estimateSource != 'GTMotive') {
              this.showRepairTypeUpdateMessage(result.data.repairServiceTypes, this.repairTypesArray);
              const queryParams = location.search;
              if (queryParams.indexOf('?mode=edit') !== -1) {
                this.router.navigate([ `/repair/booking/${this.repairGuid}/estimate` ], { queryParams: { mode: 'edit' } });
              } else {
                this.router.navigate([ `/repair/booking/${this.repairGuid}/estimate` ]);
              }
            }
          }
        }

        //

        this.commonService.hideLoading();
      },
      complete: () => {
        this.commonService.hideLoading();
      }
    });
  }

  /**
   * check gtEstimate permission
   */
  checkGTEstimatePermission(): boolean {
    const { orgPackages = [] } = this.userPermission;
    const repairEstimatePackage = orgPackages.filter((orgPackage: any) => {
      return orgPackage.automotiveServiceName === 'Repair Estimate';
    });

    if (repairEstimatePackage && repairEstimatePackage.length > 0) {
      const { permissions } = repairEstimatePackage[0];
      const gtEstimatePermission = permissions.filter((perm: any) => {
        return perm.permissionName === 'gt.estimate' || perm.permissionName === 'gt.estimate.vinquery';
      });
      if (gtEstimatePermission && gtEstimatePermission.length > 0) {
        return true;
      }
    }
    return false;
  }

  /**
   * go to gtEstimate
   */
  redirectToGtEstimate(): void {
    this.commonService.showLoading();
    this.monitorService.logEvent('redirectToGtEstimate', [ 'RepairPartsComponent', 'addenda-repair', {
      repairId: this.repairGuid
    } ]);
    this.repairEstimateService.createEstimateFromRepair(this.repairGuid).subscribe({
      next: (resp: any) => {
        if (resp) {
          location.href = resp.data.url;
          sessionStorage.setItem('update', '1');
          sessionStorage.setItem('current-url', this.router.url.toString());
          sessionStorage.setItem('initial-repair-types', JSON.stringify(this.repairTypesArray));
        }
      },
      complete: () => {
        this.commonService.hideLoading();
      }
    });
  }

  /**
   * on status change
   */
  onStatusChange(output: IControlValue): void {

    const index = output.name?.replace('ind-status-', '');
    this.individualStatusModels[index] = { ...this.individualStatusModels[index], selectedOption: output.value }

    const currentFormData = this.formData.find(x => x.partGuid == this.parts[index]?.partGuidId);
    if (currentFormData) {
      currentFormData.status = output.value?.name;
    } else {
      this.formData.push(({
        'status': output.value?.name,
        'partGuid': this.parts[index]?.partGuidId
      }));
    }

    this.saveButtonModel.type = 'primary';
  }

  /**
   * show dropdown
   * @param statusId 
   */
  showDropdown(index: number, partId: string = ''): void {
    this.editMode[index] = !this.editMode[index];

    if (this.editMode.some(item => item === true)) {
      this.saveButtonModel.type = 'primary';
    } else {
      this.saveButtonModel.type = 'disabled';
    }

    if (this.editMode[index] == false) {
      const part = this.parts.find(q => q.partId == partId);

      if (part) {
        this.individualStatusModels[index] = {
          ...this.individualStatusModels[index],
          selectedOption: { 'name': part.status, 'id': part.statusId }
        }
      }

      this.formData = this.formData?.filter(x => x.partGuid != part.partGuidId);
    }
  }

  /**
   * open part popup
   */
  openPartPopup(): void {
    // If Source does not exists then needs to check orgs packages if GT estimate is enabled or not 
    // if yes redirect to GTEstimate else show create manual estimate options  
    if (this.estimateSource == 'GTMotive') {
      this.redirectToGtEstimate();
    } else {
      if (this.checkGTEstimatePermission()) {
        //this.readOnlyMode = true;
        this.commonService.showToast(0, 'msg_gt_permission');
        return;
      }

      const dialogRef = this.dialog.open(RepairEstimateOperationComponent, {
        data: {
          'repairId': this.repairGuid
        },
        width: '85vw',
        height: '90vh',
        panelClass: 'operation-panel',
        autoFocus: false
      });

      dialogRef.afterClosed().subscribe({
        next: (response: any) => {
          if (response) {
            this.afterOperation = true;
            this.getRepairDetails();
          }
        }
      });
    }
  }

  /**
   *
   * @param repairServiceTypes
   */
  private getInitialRepairTypes(repairServiceTypes: any): void {
    repairServiceTypes.forEach((serviceType) => {
      this.repairTypesArray.push(serviceType.repairServiceType);
    });
  }

  /**
   * show repair type update message
   */
  showRepairTypeUpdateMessage(existingRepairTypes: any, initialRepairTypes: any[]): void {

    const currentRepairServiceTypes: any[] = existingRepairTypes;
    const currentRepairTypesArray = [];
    currentRepairServiceTypes?.forEach((serviceType) => {
      if (typeof serviceType == 'object') {
        currentRepairTypesArray.push(serviceType.repairServiceType);
      } else {
        currentRepairTypesArray.push(serviceType);
      }

    });

    if (currentRepairTypesArray.length > 0) {
      const newRepairTypes: any = currentRepairTypesArray.filter(q => !initialRepairTypes.includes(q));

      if (newRepairTypes.length > 0) {
        this.commonService.showInfoToast(1000, `Repair Type was updated: Added ${newRepairTypes.join(',')}`);
        this.repairTypesArray = [ ...this.repairTypesArray, ...newRepairTypes ];
      }
    }
  }
}
