/**
 * hours validator
 * @returns
 */
export function hourValidator(): ValidatorFn {
  return (control: AbstractControl): { [key: string]: any } | null => {
    const regex = /^([01][0-9]|2[0-3])$/;
    const value = control.value;
    if (!value || regex.test(value)) {
      return null;
    } else {
      return { 'invalidTimeFormat': true };
    }
  };
}


/**
 * minutes  validator
 * @returns
 */
export function minutesValidator(): ValidatorFn {
  return (control: AbstractControl): { [key: string]: any } | null => {
    const regex = /^([0-5][0-9])$/;
    const value = control.value;
    if (!value || regex.test(value)) {
      return null;
    } else {
      return { 'invalidTimeFormat': true };
    }
  };
}

/**
 *
 * @param confirmEmailInput
 * @returns
 */
export function bookingOutValidators(bookingInControl: ChqWidgetsInputModel): any {
  let bookingDateOut: FormControl;
  let bookingDateIn: FormControl;
  return (control: FormControl) => {
    if (!bookingDateOut) {
      bookingDateOut = control;
      bookingDateIn = bookingInControl.formControl;

      bookingDateIn.valueChanges.subscribe(() => {
        bookingDateOut.updateValueAndValidity({ onlySelf: true });
      });
    }
    if (new Date(bookingDateIn.value) > new Date(bookingDateOut.value)) {
      return { invalid: true };
    }
    return null;
  };
}
