import { Component, Inject, OnInit } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import * as dayjs from 'dayjs';
import { ChqWidgetsButtonModel } from 'src/app/model/chq-widgets-button-model';
import { CommonService } from 'src/app/services/common/common.service';
import { MonitorService } from 'src/app/services/monitor-service/monitor.service';

@Component({
  selector: 'app-schedule-pickup-date-popup',
  templateUrl: './schedule-pickup-date-popup.component.html',
  styleUrls: [ './schedule-pickup-date-popup.component.scss' ]
})
export class SchedulePickupDatePopupComponent implements OnInit {

  public InputModelDateTime = {
    placeHolder: 'pickup_date',
    label: 'pick_up_date_time',
    name: '',
    type: 'datetime',
    maxDate: dayjs().add(6, 'month').toDate(),
    minDate: dayjs().subtract(1, 'minute').toDate(),
    value: null,
    error: false,
    errorMessage: '',
  }

  public confirmButton: ChqWidgetsButtonModel = {
    label: 'save',
    type: 'disabled',
    onclick: this.continue.bind(this)
  }

  public PickUpDate = null;

  /**
   * constructor
   * @param dialogRef 
   * @param data 
   * @param changeDedectionRef 
   */
  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dialogRef: MatDialogRef<SchedulePickupDatePopupComponent>,
    private monitorService:MonitorService,
    private commonService:CommonService

  ) {}

  /**
   * ngOnInit
   */
  ngOnInit(): void {
    this.monitorService.logEvent('ngOnInit', [ 'SchedulePickupDatePopupComponent', 'addenda-repair' ]);
    if(this.data.pickupDate !== null){
      this.PickUpDate = this.data.pickupDate;
      if(this.data.pickupDate.indexOf('Z') === -1){
        this.PickUpDate = this.PickUpDate + 'Z';
      }
      
      if(this.data.callSource === 'schedule') {
        if(new Date(this.PickUpDate) > new Date()){         
          this.confirmButton = { ...this.confirmButton, type: 'primary' };
        }
      } else {
        if(new Date(this.PickUpDate) < new Date()){
          this.confirmButton = { ...this.confirmButton, type: 'primary' };
        }
      }
      
    }
    if(this.data.callSource === 'pickedUp') {
      
      this.InputModelDateTime.maxDate = dayjs().add(1, 'minute').toDate();
      this.InputModelDateTime.minDate = dayjs().subtract(6, 'month').toDate();
    }
  }

  /**
   * close
   */
  close():void{
    this.dialogRef.close();
  }

  /**
   * continue
   * 
   */
  continue(): void {
    this.monitorService.logEvent('continue', [ 'SchedulePickupDatePopupComponent', 'addenda-repair' ]);
    this.dialogRef.close({ 'PickUpDate': this.PickUpDate });
  }

  /**
   * updateInput
   * 
   */
  updateInput({ value }): void {
    this.InputModelDateTime.error=false;
    if(this.data.callSource === 'pickedUp'){
      if(new Date(value) > new Date()){
        if(dayjs(new Date(value)).format('YYYY-MM-DD hh:mm') == dayjs().format('YYYY-MM-DD hh:mm')){
          this.InputModelDateTime.error=false;
          this.PickUpDate = new Date(value.toString());
          this.confirmButton = { ...this.confirmButton, type: 'primary' };
          return;
        }
        this.InputModelDateTime.error=true;
        this.InputModelDateTime.errorMessage='pick_up_date_cannot_set_as_future_date';
        this.confirmButton = { ...this.confirmButton, type: 'disabled' };
        return;
      }
    } else{
      if(new Date(value) < new Date()){
        if(dayjs(new Date(value)).format('YYYY-MM-DD hh:mm') == dayjs().format('YYYY-MM-DD hh:mm')){
          this.InputModelDateTime.error=false;
          this.PickUpDate = new Date(value.toString());
          this.confirmButton = { ...this.confirmButton, type: 'primary' };
          return;
        }
        this.InputModelDateTime.error=true;
        this.InputModelDateTime.errorMessage='Pick_up_date_cannot_set_as_past_date';
        this.confirmButton = { ...this.confirmButton, type: 'disabled' };
        return;
      }
    }
    this.PickUpDate = new Date(value.toString());
    this.confirmButton = { ...this.confirmButton, type: 'primary' };
  }

}
