import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { SeverityLevel } from '@microsoft/applicationinsights-web';
import { TranslateService } from '@ngx-translate/core';
import { CdTimerComponent } from 'angular-cd-timer';
import * as dayjs from 'dayjs';
import { catchError, forkJoin, of } from 'rxjs';
import { AlertDialogComponent } from 'src/app/dialogs/alert-dialog/alert-dialog.component';
import { RepairJobTimerStatus } from 'src/app/model/chq-widgets-input-model';
import { menuItem } from 'src/app/model/menu';
import { BookingService } from 'src/app/services/booking/booking.service';
import { CommonService } from 'src/app/services/common/common.service';
import { InspectionService } from 'src/app/services/inspection-service/inspection.service';
import { MonitorService } from 'src/app/services/monitor-service/monitor.service';

@Component({
  selector: 'app-repair-stages',
  templateUrl: './repair-stages.component.html',
  styleUrls: [ './repair-stages.component.scss' ]
})
export class RepairStagesComponent implements OnInit {
  public header = {
    title: 'Upload Images',
    step: 0,
    description: '',
    stepName: 'back_bulk_upload',
    rightActionText: 'delete_all',
    rightActionIcon: 'delete',
    isRepairModule: true,
    showTitle: true
  }
  public jobList: any = null;
  public repairDetail: any;
  public currentActiveIndex: number = 0;
  public currentJobStarted: boolean = false;
  public currentJobStopped: boolean = false;
  public currentJobPaused: boolean = false;
  public timeDuration: number = 0;
  public currentInspectionTemplate: any;
  public inspectionTemplateId: number;
  public enableCompleteButton: boolean;
  public enableNotifyButton: boolean;
  private repairId: string = '';
  public currentActiveTab = 'tasks';
  public isJobLoading:boolean = true;
  public inspectionTemplates: any;
  public reinitialize: boolean;
  public currentJobList: any = null;
  public menuItems: menuItem[] = [];
  public repairStatus: string = '';
  public inspectionId: number;
  private isResumed: boolean = false;
  public notifyData: any = {};
  public outBoundStatus: string = '';
  @ViewChild('basicTimer', { static: false }) basicTimer: CdTimerComponent;

  /**
   * constructor
   */
  constructor(
    private bookingService: BookingService,
    public commonService: CommonService,
    private inspectionService: InspectionService,
    private router: Router,
    private translateService: TranslateService,
    private route: ActivatedRoute,
    private monitorService: MonitorService) {

  }

  /**
   * set tab active
   */
  setTabActive(tab: string): void {
    this.currentActiveTab = tab;
  }

  /**
   * start job timer
   */
  startJobTimer(): void {
    const bookingStartDate = this.repairDetail?.booking?.startDate;
    const minutes = dayjs(new Date()).diff(dayjs(bookingStartDate), 'days');
    if(minutes < 0){
      this.commonService.showToast(1000, this.translateService.instant('cannot_start_future_date'));
      return;
    }
    const repairId = this.currentJobList.repairJobGuid;
    this.commonService.showLoading();
    this.updateJobTimerStatus(repairId, RepairJobTimerStatus.Start);
  }

  /**
   * pauseJobTimer
   */
  pauseJobTimer(): void {
    const repairId = this.currentJobList.repairJobGuid;
    this.commonService.showLoading();
    this.updateJobTimerStatus(repairId, RepairJobTimerStatus.Pause);
  }

  /**
   * repair status
   */
  get currentStatus():boolean{
    const statusList = [ 'approved', 'repairinprogress', 'partsordered', 'partsorderedpartially', 'readytoinvoice', 'invoiced', 'paid', 'complete' ];
    return statusList.includes(this.repairStatus?.toLowerCase());
  }

  /**
   * update job timer status
   */
  updateJobTimerStatus(repairId:string, status:string):void{
    if(this.currentJobList?.endDate){
      this.commonService.hideLoading();
      return;
    }
    const statusList = [ 'approved', 'repairinprogress', 'partsordered', 'partsorderedpartially', 'readytoinvoice', 'invoiced', 'paid', 'complete' ];
    if(!statusList.includes(this.repairStatus?.toLowerCase())){
      this.commonService.hideLoading();
      this.commonService.openAlertDialog(this.translateService.instant('warning'), this.translateService.instant('start_job'));
      return;
    }
    const activeJobList = this.jobList?.filter((x) => {
      return x.startDate && !x.endDate;
    })
    if (activeJobList && activeJobList?.length > 0 && status === RepairJobTimerStatus.Start) {
      if (activeJobList[0].id !== this.currentJobList.id) {
        this.commonService.hideLoading();
        this.commonService.openAlertDialog(this.translateService.instant('warning'), this.translateService.instant('job_in_progress', { value: activeJobList[0].name }));
        return;
      }
    }

    this.bookingService.updateRepairTimerStatus(repairId, status).subscribe({
      next: () => {
        let skip = false;
        if (status === RepairJobTimerStatus.Start) {
          this.currentJobStarted = true;
          if (this.currentJobPaused) {
            this.currentJobPaused = false;
            try{
              this.basicTimer?.resume();
              this.isResumed = true;
            }catch(e){
              this.isResumed = false;
            }
          }else{
            if(this.repairDetail?.status.toLowerCase() !== 'repairinprogress'){
              this.bookingService.getRepairDetailByGuid(this.repairDetail?.repairGuid).subscribe({
                next: async (result: any) => {
                  if (result?.data) {
                    this.commonService.repairDetails.next(result.data);
                    this.commonService.bookingId.next(result.data.repairNumber);                          
                  }
                },
                error: (err: any) => {
                  this.monitorService.logException(err, SeverityLevel.Error);
                }
              });
            }
            skip = true;
          }
        } else if (status === RepairJobTimerStatus.Stop) {
          this.currentJobStopped = true;
          if (this.currentJobPaused) {
            this.basicTimer?.stop();
            this.currentJobPaused = false;
          }
        } else {
          this.basicTimer?.stop();
          this.currentJobPaused = true;
        }
        this.outBoundStatus = 'Repair In Progress';
        this.repairStatus = 'RepairInProgress';
        this.getJobs(true, skip);
      },
      error: () => {
        this.commonService.hideLoading();
      }
    })
  }

  /**
   * start job timer
   */
  stopJobTimer(): void {
    const repairId = this.currentJobList.repairJobGuid;
    if(this.currentJobList.endDate){
      return;
    }
    const mandatoryTasks = this.currentJobList.repairJobTasks.filter((task) => {
      return task.isMandatory === false && task.completedDate === null;
    }).length
    if(mandatoryTasks > 0){
      const dialogRef = this.commonService.openConfirmationOkDialog(this.translateService.instant('confirm_action'), this.translateService.instant('confirm_normal_action'));
      dialogRef.afterClosed().subscribe((action: boolean) => {
        if (action) {
          this.commonService.showLoading();
          this.updateJobTimerStatus(repairId, RepairJobTimerStatus.Stop);
        } else {
          this.commonService.hideLoading();
        }
      });
      return;
    }
    this.commonService.showLoading();
    this.updateJobTimerStatus(repairId, RepairJobTimerStatus.Stop);
  }

  /**
   * get repair and template details
   * 
   */
  loadApiData(id: string): void {
    if (id && id != '0') {
      this.commonService.showLoading();
      const basicApi = {
        inspectionList: this.inspectionService.getInspectionTemplateList(),
        repairDetail: this.bookingService.getRepairDetailByGuid(id)
      }
      this.isJobLoading = true;
      const basicServerDetails = forkJoin(basicApi).pipe(
        catchError(error => of(error))
      )
      basicServerDetails.subscribe({
        next: (basicServerMap: any) => {
          const { inspectionList, repairDetail } = basicServerMap;
          if (repairDetail) {
            const { vehicle, repairNumber, status } = repairDetail.data;
            this.setRepairStatus(status);
            this.repairDetail = repairDetail.data;
            this.inspectionId = vehicle.inspectionId;
            // Send booking data in observable
            this.commonService.repairDetails.next(repairDetail.data)
            this.commonService.bookingId.next(repairNumber);

            this.notifyData = {
              repairGuid: this.repairDetail.repairGuid,
              domainId: this.repairDetail.domainId,
              communicationList: this.repairDetail.booking.communicationTypeId,
              communicatedList: this.repairDetail.communicationEvents
            };
          }
          const repairInspectionTemplate = inspectionList?.data;
          this.outBoundStatus = this.repairDetail.outboundStatus;
        
          if (repairInspectionTemplate?.length > 0) {
            this.currentInspectionTemplate = repairInspectionTemplate[0];
            this.inspectionTemplateId = repairInspectionTemplate[0].id
          }

          const promiseApi: any = {};
          promiseApi.inspectionTemplate = this.inspectionService.getInspectionTemplate(this.inspectionTemplateId);
          const serverDetails = forkJoin(promiseApi).pipe(
            catchError(error => of(error))
          )

          serverDetails.subscribe({
            next: (serverMap: any) => {
              const { inspectionTemplate, } = serverMap;
              this.inspectionTemplates = inspectionTemplate.inspectionItemTemplates;
              this.getJobs();
              this.isJobLoading = false;
            },
            error: () => {
              this.commonService.hideLoading();
              this.isJobLoading = false;
            }
          });
        },
        error: ()=>{
          this.isJobLoading = false;
        }
      });

    }
  }

  /**
   * get repair details
   */
  getJobs(backgroud?: boolean, skipLoad?: boolean): void {
    const repairId = this.route.parent?.snapshot.paramMap.get('id');
    if (!backgroud) {
      this.commonService.showLoading();
    } else {
      this.commonService.hideLoading();
    }
    this.bookingService.getRepairTask(repairId).subscribe({
      next: async (result: any) => {
        this.jobList = result.data;  
        
        if(this.jobList?.length === 0){
          this.commonService.hideLoading();  
          return;
        }

        const unfinishedJobs = this.jobList.filter((j)=>{
          return !j.endDate;
        });

        const startedJobs = this.jobList.filter((k) => { return k.startDate; });

        this.enableNotifyButton = false;
        if (startedJobs?.length > 0) {
          if(this.outBoundStatus === 'Repair In Progress') {
            this.enableNotifyButton = true;
          }
        }

        if (unfinishedJobs?.length === 0) {
          this.enableCompleteButton = true;
        } else {
          let currentJobExists = false;
          unfinishedJobs.forEach((job) => {
            if (job.repairJobTasks && job.repairJobTasks.length > 0) {
              const hasMandetoryTask = job.repairJobTasks.filter((jb)=>{
                return jb.isMandatory === true && jb.completedDate === null;
              });
              if(hasMandetoryTask.length > 0){
                currentJobExists = true;
              }
            }
          });

          this.enableCompleteButton = !currentJobExists;
        }

        if (!backgroud) {
          this.menuItems = this.jobList.map((job: any) => {
            return {
              name: job.name,
              icon: '',
              displayName: job.taskCompletionPercentage,
              url: '',
              title: job.name,
              subTitle: '',
              color: job.color,
              permission: true
            }
          });

          this.setActiveJob(this.menuItems[this.currentActiveIndex]);
        }else{
          this.updateActiveTask(this.currentActiveIndex, skipLoad, backgroud);
        }

        this.commonService.hideLoading();
      },
      error: () => {
        this.commonService.hideLoading();
      }
    });
  }

  /**
   * set repair status
   */
  setRepairStatus(status: string): void {
    this.repairStatus = status;
  }

  /**update active task
   * 
   */
  updateActiveTask(currentActiveIndex:number, skipLoad:boolean, isBackground?:boolean):void{
    this.currentJobList = this.jobList[currentActiveIndex];
    this.currentJobList = { ...this.currentJobList };
    this.menuItems[currentActiveIndex].displayName = this.currentJobList.taskCompletionPercentage;
    if (this.currentJobList?.timerLog) {
      const { startDate, endDate, statusName } = this.currentJobList.timerLog;
      const currentDate = dayjs(new Date());
      const startingDate = dayjs(startDate);
      this.currentJobStarted = true;
      
      if (RepairJobTimerStatus.Pause === statusName) {
        this.currentJobPaused = true;
        if (endDate) {
          const pausedDate = dayjs(endDate);
          const durationFromPause = pausedDate.diff(startingDate, 'seconds');
          this.timeDuration = durationFromPause;
          this.isResumed = false;
          setTimeout(() => {
            this.basicTimer?.stop();
          });
        }
      } else if (RepairJobTimerStatus.Stop === statusName) {
        this.currentJobStopped = true;
        if (endDate) {
          const pausedDate = dayjs(endDate);
          const durationFromPause = pausedDate.diff(startingDate, 'seconds');
          this.timeDuration = durationFromPause;
          this.isResumed = false;
          setTimeout(() => {
            this.basicTimer?.stop();
          });
        }
      } else {
        if(!skipLoad && isBackground){
          return;
        }
        this.timeDuration = currentDate.diff(startingDate, 'seconds');
        if (!skipLoad && this.isResumed) {
          return;
        }
        setTimeout(() => {
          if (skipLoad) {
            this.basicTimer?.start();
          }else{
            if(!isBackground){
              this.basicTimer?.resume();
            }
            this.isResumed = true;
          }
        });
      }

    }
  }

  /**
   * setActiveJob
   */
  setActiveJob(menuItem: menuItem): void {
    const currentMenu = this.jobList.filter((job) => {
      return job.name === menuItem.name;
    });

    if (currentMenu.name === menuItem.name) {
      return;
    }

    if (currentMenu?.length > 0) {
      const menuIndex = this.menuItems.findIndex((menu: menuItem) => {
        return menu.name === menuItem.name;
      });
      this.menuItems.forEach((menuItm: menuItem) => {
        menuItm.active = false;
      });
      menuItem.active = true;
      this.currentJobStarted = false;
      this.currentJobStopped = false;
      this.currentJobPaused = false;
      this.timeDuration = 0;
      this.menuItems[menuIndex] = menuItem;
      this.menuItems = [ ...this.menuItems ];
      this.reinitialize = true;
      this.currentJobList = null;
      this.currentActiveTab = 'tasks';
      setTimeout(() => {
        this.currentActiveIndex = menuIndex;
        this.currentJobList = currentMenu[0];
        this.reinitialize = false;
        if (this.currentJobList?.timerLog) {
          const { startDate, endDate, statusName } = this.currentJobList.timerLog;
          const currentDate = dayjs(new Date());
          const startingDate = dayjs(startDate);
          this.currentJobStarted = true;
          if (RepairJobTimerStatus.Pause === statusName) {
            this.currentJobPaused = true;
            if (endDate) {
              const pausedDate = dayjs(endDate);
              const durationFromPause = pausedDate.diff(startingDate, 'seconds');
              this.timeDuration = durationFromPause;
              setTimeout(() => {
                this.basicTimer.stop();
              });
            }
          } else if (RepairJobTimerStatus.Stop === statusName) {
            this.currentJobStopped = true;
            if (endDate) {
              const pausedDate = dayjs(endDate);
              const durationFromPause = pausedDate.diff(startingDate, 'seconds');
              this.timeDuration = durationFromPause;
              setTimeout(() => {
                this.basicTimer?.stop();
              });
            }
          } else {
            this.timeDuration = currentDate.diff(startingDate, 'seconds');
            setTimeout(() => {
              this.basicTimer.resume();
            });
          }
        }
      });

    }
  }

  /**
   * complete job
   */
  completeJob(): void {
    const currentObjectId = this.route.parent?.snapshot.paramMap.get('id');

    const inprogressTask = this.jobList.filter((jb)=>{
      return jb.startDate && !jb.endDate;
    });

    if(inprogressTask.length > 0){
      this.commonService.openAlertDialog(this.translateService.instant('warning'), this.translateService.instant('stop_job_complete')).afterClosed().subscribe();
      return;
    }
    const statuses = [ 'readytoinvoice', 'invoiced', 'paid', 'complete' ];
    if (statuses.includes(this.repairStatus?.toLowerCase())) {
      this.router.navigate([ `repair/booking/${currentObjectId}/invoices` ]);
      return;
    }

    this.commonService.openConfirmationOkDialog('confirm_completing', 'complete_repair_process').afterClosed().subscribe({
      next: (val: boolean) => {
        if (val) {
          this.commonService.showLoading();
          this.bookingService.updateRepairStatus('ReadytoInvoice', currentObjectId, '').subscribe({
            next: () => {
              this.commonService.hideLoading();
              this.router.navigate([ `repair/booking/${currentObjectId}/invoices` ]);
            }
          });
        }
      },
      error: () => {
        this.commonService.hideLoading();
      }
    })

  }

  /**
   * update title according to option selected
   * @param $event 
   */
  handleUpdateMode($event): void {
    if ($event.mode === 'single') {
      this.header = { ...this.header, title: 'back_smart_photo' }
    } else {
      this.header = { ...this.header, title: 'back_bulk_upload' }
    }
  }

  /**
   * ngOninit
   */
  ngOnInit(): void {
    const currentObjectId = this.route.parent?.snapshot.paramMap.get('id');
    this.loadApiData(currentObjectId);
  }
}
